.container{
    margin-top: 25px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 25px;
    justify-items: center;
    align-items: center;
}



@media screen and (max-width: 768px ){
    .container{
        grid-template-columns: repeat(1, 1fr);;
    }
}
